/* write your css here */
*{
    margin:0;
    padding:0;
    box-sizing:border-box;

}
body{
    min-height:100vh;
    font-family:"poppins",sans-serif;
    

}
.title{
    font-size:3rem;
    margin:6vh;
    padding: 1vh 5vw;
    border:7px dashed rgb(1,17,160);
    border-radius:20px;
    
}
.container{
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction:column;
}
.cards{
    display:flex;
    
    justify-content:center;
    flex-wrap:wrap;
    max-width:1300px;
    margin:0 auto;  
}
.card{
    width:400px;
    height:max-content;
    margin:1rem;
    padding:1rem;
    display:flex;
    flex-direction:column;
    border-radius:center;
    align-items:center;
    box-shadow: rgb(0,0,0,0.24) 0px 3px 8px;
}
.image{
    width:380px;
    aspect-ratio:1/1;
    object-fit:cover;
    
}
.tour-info{
    margin:20px 5px;

}
.tour-price{
    color:#1faa59;
    font-size:1.3rem;


}
.tour-name{
    font-size:1.5rem;
}
.read-more{
    color:#12b0e8;
    cursor:pointer;
     
}
.btn-red{
    margin-top:18px;
    padding:10px 80px;
    border:1px solid #b4161b;
    cursor:pointer;
    border-radius:10px;
    font-size:18px;
    font-weight:bold;
    background-color:#b4161b21;

}
.btn-red:hover{
    background-color:red;
    color:white;
    transition:all 0.2s;


}
.refresh{
    height:100vh;
    
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction:column;
}
.btn-white{
    margin-top:18px;
    padding:10px 80px;
    border:1px solid white;
    cursor:pointer;


}
.btn-white:hover{
    background-color:white;
    color:black;
    transition:all 0.2s;

}





